.bubble-animation .bar-header > * {
  -webkit-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}
.bubble-animation .scroll-content {
  -webkit-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in;
}
.bubble-animation .ion-ios7-close-outline {
  -webkit-transition: bottom 0.3s ease-out;
  transition: bottom 0.3s ease-out;
}
.bubble-in .bar-header {
  -webkit-animation: bubble-in-header 1s forwards;
  overflow: hidden;
}
.bubble-in .bar-header > *:not(.right-buttons) {
  opacity: 0;
}
.bubble-in .bar-header .right-buttons {
  right: 4px;
}
.bubble-in .bar-header .right-buttons:after {
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -8px;
  font-family: "Ionicons";
  font-size: 20px;
  line-height: 40px;
  content: "\f122";
}
.bubble-in .scroll-content {
  opacity: 0.2;
  transform: scale(0.8) translateY(-10%);
}
.bubble-in .ion-ios7-close-outline {
  bottom: 26px;
  -webkit-transition-delay: 0.6s;
  transition-delay: 0.6s;
}
.bubble-confirm .bar-header .right-buttons {
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s;
  -webkit-transform: translateY(-100%);
  transform: translateY(-100%);
}
.bubble-confirm .bar-header .right-buttons:after {
  -webkit-animation: bubble-done-rotate 1s infinite linear;
  margin-left: -12px;
  font-size: 25px;
  content: "\f29d";
}
.bubble-done .bar-header .right-buttons {
  -webkit-transform: translateY(-100%);
  transform: translateY(-100%);
}
.bubble-out .bar-header {
  -webkit-animation: bubble-out-header 0.5s;
}
.bubble-out .bar-header > *:not(.right-buttons) {
  -webkit-transition-delay: 0.3s;
  transition-delay: 0.3s;
}
.bubble-out .scroll-content {
  -webkit-transition-delay: 0.3s;
  transition-delay: 0.3s;
}
@-webkit-keyframes bubble-in-header {
  30% {
    top: 0;
    left: 50%;
    margin-top: 0px;
    margin-left: -25px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }
  100% {
    top: 50%;
    left: 50%;
    margin-top: -25px;
    margin-left: -25px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }
}
@-webkit-keyframes bubble-out-header {
  0% {
    top: 50%;
    left: 50%;
    margin-top: -25px;
    margin-left: -25px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }
  70% {
    top: 0;
    left: 50%;
    margin-top: 0px;
    margin-left: -25px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
  }
  100% {
    left: 0;
    margin-left: 0;
    width: 100%;
    height: auto;
    border-radius: 0;
  }
}
@-webkit-keyframes bubble-done-rotate {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@-webkit-keyframes bubble-confirm-fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes bubble-confirm-fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
